import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/Home.vue';
import About from './views/About.vue';
import Cv from './views/Cv.vue';

import cvEs from '@/utils/cv/es';
import cvEn from '@/utils/cv/en';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
    },
    {
      path: '/about',
      name: 'about',
      component: About,
      children: [
        { path: '', redirect: { name: 'es' } },
        {
          path: 'es', name: 'es', component: Cv, props: { cv: cvEs },
        },
        {
          path: 'en', name: 'en', component: Cv, props: { cv: cvEn },
        },
      ],
    },
  ],
});
