<template>
  <div
    :class="$style.hoverShine"
    @click="showHide">
    <div :class="$style.title">
      <div :class="$style.first">
        <span><b>{{ role }}</b></span>
        <span>{{ company }}</span>
      </div>
      <div :class="$style.second">
        <span>
          {{ moment(startDate).format("MM/YYYY") }}
          - {{ finishDate ? moment(finishDate).format("MM/YYYY") : 'Actualidad' }}
        </span>
        <i>
          ({{ jobDuration }})
        </i>
      </div>
    </div>
    <div
      v-if="showSummary"
      :class="$style.summary">
      {{ summary }}
    </div>
    <div :class="$style.keywords">
      <span
        v-for="m in keywords"
        :key="m"
        :class="$style.tag">
        {{ m }}
      </span>
    </div>
  </div>
</template>

<script>
import moment from 'moment';

export default {
  name: 'ProfessionalJob',
  props: {
    role: String,
    company: String,
    startDate: String,
    finishDate: String,
    summary: String,
    keywords: Array,
  },
  data() {
    return {
      showSummary: true,
    };
  },
  computed: {
    jobDuration() {
      let finish;
      if (this.finishDate) {
        finish = moment(this.finishDate);
      } else {
        finish = moment();
      }
      const years = finish.diff(this.startDate, 'years');
      const months = finish.diff(this.startDate, 'months') - (years * 12);
      let retValue = '';
      retValue += years > 0 ? `${years} años` : '';
      retValue += years > 0 && months > 0 ? ', ' : '';
      retValue += months > 0 ? `${months} meses` : '';
      return retValue;
    },
  },
  methods: {
    moment,
    showHide() {
      this.showSummary = !this.showSummary;
    },
  },
};
</script>

<style module lang="scss">
@import '@/style.scss';

.title {
  display: flex;
  flex-direction: row;
  margin-bottom: 10px;
}

.first, .second {
  display: flex;
  flex-direction: column;
}

.first {
  flex: 1;
}

.second {
  text-align: right;
}

.summary {
  text-align: justify;
  margin: 10px 0;
  padding: 10px;
  border: 10px #02253c dashed;
}

.keywords {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.tag {
  display: flex;
  border: 2px $secondaryColor solid;
  color: $secondaryColor;
  border-radius: 3px;
  padding: 0 5px;
  margin: 5px;
  font-size: 14px;
  box-shadow: inset 0 0 10px darken($primaryColor, $itemDarknessHover);
}

.link {
  color: $secondaryColor !important;
}

.hoverShine {
  box-shadow: inset 0 0 5px darken($primaryColor, $heavyDarkness);
  background: darken($primaryColor, $heavyDarkness);
  padding: 10px;
  margin: 3px;
  cursor: pointer;
}

.hoverShine:hover {
  background-color: darken($primaryColor, $heavyDarknessHover);
}
</style>
