@import '@/style.scss';
@import url('https://fonts.googleapis.com/css?family=Open+Sans');

body {
  background-color: $primaryColor;
  margin: 0,;
  padding: 0;
}

.app {
  font-family: 'Open Sans', sans-serif;
  text-align: center;
  color: #fff;
  text-shadow: 0 0.05rem 0.1rem rgba(0, 0, 0, .5);
}

.content {
  padding: 60px 30px;
}

.nav {
  padding: 20px;

  a {
    text-decoration: none;
    font-weight: bold;
    color: rgba(255, 255, 255, .5);
  }
}

.title {
  font-size: 2.5rem;
  text-transform: capitalize;
  margin: 0;
}

.overlay {
  position: fixed;
  height: 100%;
  width: 100%;
  box-shadow: inset 0 0 5rem rgba(0, 0, 0, .75);
  pointer-events: none;
}
