<template>
  <div :class="$style.content">
    <div :class="$style.header">
      <h2 :class="$style.title">{{ title }}</h2>
      <span :class="$style.date">{{ date }}</span>      
    </div>
    <div>
      <!-- EDUCATION -->
      <h3><font-awesome-icon icon="puzzle-piece" /> Educación ({{education.length}})</h3>
      <education
        v-for="e in education"
        :key="e.DEGREE"
        :degree="e.DEGREE"
        :institution="e.INSTITUTION"
        :start-date="e.START_DATE"
        :finish-date="e.FINISH_DATE"
        :merits="e.MERITS"/>
      <!-- JOBS -->
      <h3><font-awesome-icon icon="puzzle-piece" /> Experiencia Profesional</h3>
      <!-- JOBS - PROFESSIONAL -->
      <h4>Laboral ({{professionalJobs.length}})</h4>
      <professional-job
        v-for="e in professionalJobs"
        :key=" `${e.ROLE}-${e.COMPANY}`"
        :role="e.ROLE"
        :company="e.COMPANY"
        :start-date="e.START_DATE"
        :finish-date="e.FINISH_DATE"
        :summary="e.SUMMARY"
        :keywords="e.KEYWORDS"/>
      <!-- JOBS - VOCATIONAL -->
      <h4>Vocacional ({{vocationalJobs.length}})</h4>
      <vocational-job
        v-for="e in vocationalJobs"
        :key="e.ROLE"
        :role="e.ROLE"
        :company="e.COMPANY"
        :start-date="e.START_DATE"
        :finish-date="e.FINISH_DATE"
        :summary="e.SUMMARY"
        :points="e.POINTS"/>
    </div>
  </div>
</template>

<script>
import Education from '@/components/Education';
import ProfessionalJob from '@/components/ProfessionalJob';
import VocationalJob from '@/components/VocationalJob';

import t from '@/utils/messages';

export default {
  name: 'Cv',
  components: {
    Education,
    ProfessionalJob,
    VocationalJob,
  },
  props: {
    cv: {
      type: Object,
      required: true,
    },
  },
  computed: {
    title() {
      return this.cv.TITLE;
    },
    date() {
      return `${this.cv.DATE} ${process.env.VUE_APP_TITLE}`;
    },
    education() {
      return this.cv.EDUCATION;
    },
    professionalJobs() {
      return this.cv.JOBS.PROFESSIONAL;
    },
    vocationalJobs() {
      return this.cv.JOBS.VOCATIONAL;
    },
  },
  methods: {
    t,
  },
};
</script>

